import model from '../../components/model/model.vue'
import swipers from '../../components/swiper/swiper.vue'
import {ajax} from '../../common/js/util.js'
export default {
	data() {
		return {
			Color: '#333333',
			value: '',
			backColor: '#FFFFFF',
			show: false,
			confirmText: '去开通',
			activityList: [],
			bannerList: [],
			icons: []
		}
	},
	components: {
		model,
		swipers
	},
	onLoad() {
		//刷新保留用户uid
		if (this.getRequest('uid')) {
			var uids = this.getRequest('uid')
			this.$store.commit('changeUid', uids)
		}
		//初始化执行
		this.init()
	},
	methods: {
		getRequest(variable) {
			var query = window.location.search.substring(1);
			var vars = query.split("&");
			for (var i = 0; i < vars.length; i++) {
				var pair = vars[i].split("=");
				if (pair[0] == variable) {
					return pair[1];
				}
			}
			return (false);
		},
		init() {
			let self = this
			let datas = {
				uid: this.$store.state.uid
			}
			let data = {
				url: '/api/gzh/index',
				data: datas,
				success: function(res){
					console.log(res)
					if(res.data.result == 0){
						if(res.data.activityList.length <= 3){
							self.activityList = res.data.activityList
						}else{
							for(let i=0;i<3;i++){
								self.activityList.push(res.data.activityList[i])
							}
						}
						self.bannerList = res.data.bannerList
						self.icons = res.data.icons
						self.$store.commit('changeVip', res.data.vipType)
						self.$store.commit('changeCustomer', res.data.customer)
					}
				}
			}
			ajax(data)
		},
		goRoom() {
			uni.navigateTo({
				url: '../vipRoom/vipRoom'
			})
		},
		goAir() {
			uni.navigateTo({
				url: '../airTicket/airTicket'
			})
		},
		goFlight() {
			if(this.$store.state.vipType == 0){
				this.show = true
				return
			}
			uni.navigateTo({
				url: '../flightTab/flightTab'
			})
		},
		cancel() {
			this.show = false
		},
		confirm() {
			this.show = false
			uni.switchTab({
				url: '/pages/vip/vip'
			})
		},
		goMsg() {
			uni.navigateTo({
				url: '../message/message'
			})
		},
		goDetail(path) {
			uni.navigateTo({
				url: '../content/content?url=' + encodeURIComponent(path)
			})
		},
		goImg(argument) {
			if(argument[0] == 3) {
				uni.navigateTo({
					url: '../content/content?url=' + encodeURIComponent(argument[1])
				})
			}
		},
		goScore() {
			uni.navigateTo({
				url: '../friends/friends'
			})
		},
		goHot() {
			uni.navigateTo({
				url: '../hotDetail/hotDetail'
			})
		},
		goNo(tit) {
			uni.navigateTo({
				url: '../noOpen/noOpen?title=' + tit
			})
		}
	}
}
